<template>
  <div id="app">
      <!-- 使用keepalive进行缓存 -->
    
      <router-view />
    
      <!-- <router-view  /> -->

  </div>
</template>

<script>
import commonHeader from "./components/data/commonHeader";
// import commonFooter from "./components/myFooter";
import { Toast } from "vant";
export default {
  name: "App",
  data() {
    return {
      path: "",
      transitionName: ""
    };
  },
  watch: {
    $route(to, from) {
      console.log("to", to, "from", from);
      let toName = to.name;

      const toIndex = to.meta.index;
      const fromIndex = from.meta.index;

      this.transitionName = toIndex < fromIndex ? "slide-right" : "slide-left";
    }
  },
  methods: {},
  mounted() {
    var exitApp = 0;
    var _this = this;
    // console.log("_this.$router.history.current.name",_this.$router.history.current.name==="Login");

    document.addEventListener("backbutton", onBackKeyDown, false);
    function onBackKeyDown() {
      //可以加一些逻辑判断，比如可以在固定的几个页面按返回键时才切换到后台
      // 返回按钮事件的事件处理函数
      if (
        _this.$router.history.current.name === "Home" ||
        _this.$router.history.current.name === "Login"
      ) {
        // alert(1);
        // this.$store.state.showGaode = false;
        if (exitApp === 0) {
          exitApp++;
          Toast("再点击一次回到主页");
          setTimeout(() => {
            exitApp = 0;
          }, 2000);
        } else if (exitApp === 1) {
          // window.cordova.plugins.backgroundMode.moveToBackground();
          navigator.Backbutton.goHome(
            function() {},
            function() {}
          );
        }
      } else {
        _this.$router.go(-1);
      }
    }
    console.log("path", this.$router.history.current.path);
    this.path = this.$router.history.current.path;
  },
  components: {
    "v-commonHeader": commonHeader
    // "v-commonFooter": commonFooter
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(245, 245, 245);
  overflow: auto;
  /* height: auto; */
  font-size: 14px;
  width: 100%;
  height: 100%;
  /* text-align: center; */
  color: #000000;
  /* margin-top: 60px; */
}
.header {
  line-height: 40px;
  text-align: center;
}
.header a {
  text-align: left;
  color: white;
  position: absolute;
  top: 3px;
  left: 0;
}
.header span {
  font-size: 20px;
}
.amap-info-content {
  /* background-color: #fdfdfd; */
  padding: 15px 10px;
  /* width:160px; */
  height: 50px;
  color: #636363;
  border-radius: 10px;
  align-items: center;
  box-shadow: 5px 5px 5px #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  /* height: 300px; */
}
.TextPosition {
  /* position: absolute;
  top: 0; */
  background-color: white;
  border-radius: 10px;
  width: 100%;
  padding: 5px;
}
.flexCont {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
  /* height: 30px; */
  color: #7f7f7f;
  font-size: 10px;
}
.Markertitle {
  font-weight: 600;
  color: #636363;
  font-size: 12px;
}
.positionAbMap {
  /* text-align: right; */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.positionAbMapCar {
  text-align: right;
  /* height: 30px; */
}
.positionAbMap img {
  width: 32px;
  height: 32px;
}
.bimImage img {
  width: 30px;
  height: 30px;
}
#markerContent {
  width: 200px;
}
#markerContent div {
  /* background: red; */
  margin: 5px 0;
}
#markerContent div span {
  font-weight: 600;
}
/* 选择框的样式 */
.mu-select-input {
  /* padding: 0 20px; */
  text-align: center;
}
.mu-input-line {
  border-bottom: none;
}
.mu-item {
  /* padding: 0; */
  /* width: 150px; */
}
.mu-list {
  padding: 10px;
}
.mu-input-line {
  background: white;
  border: none;
}
.mu-input-focus-line {
  background: white;
}
.mu-input-line.disabled {
  border: none;
}
.mu-text-field-input {
  height: 36px;
  text-align: center;
  justify-content: center;
  display: flex;
  padding: 10px;
}
.mu-input-help {
  justify-content: center;
}
input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px transparent inset !important;
  background: transparent;
}

.mu-input.full-width {
  width: 100%;
  border: none;
}

.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  transition: all 500ms;
}

.slide-right-enter {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
.slide-right-leave-to {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.slide-left-enter {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.slide-left-leave-to {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
/* 筛选框的样式 */
.queryPadding {
  padding: 8px;
}
/*  */
.dialogContent {
  color: blue;
}
.addressHeight {
  height: 30px;
  padding-top: 8px;
  font-size: 10px;
}
#realDataLookFor {
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 3px 5px;
  box-shadow: 0 0 10px #ccc;
}
*:focus {
  outline: none;
  background-color: transparent;
}
::selection {
  background: transparent;
}
::-moz-selection {
  background: transparent;
}

/* 导航栏背景色渐变色 */
.backgroundNaviBar {
  background: -webkit-linear-gradient(to left, #328efb, #4abbfe) !important;
  background: linear-gradient(to left, #328efb, #4abbfe) !important;
  color: white;
}
.titleColor {
  font-size: 12px;
  color: #ccc;
}
.rightTitleColor {
  font-size: 12px;
  color: #ccc;
}

/* 公用的border-radius */

.border-radius {
  border-radius: 2px;
}
.borderStyle {
  border-bottom: 1px rgb(236, 236, 236) solid;
}

/* 头部公用样式 */
.van-nav-bar__left {
  color: white !important;
}
.van-nav-bar .van-icon {
  color: white !important;
}
.van-nav-bar__text {
  color: white !important;
}
.van-nav-bar__title {
  color: white !important;
  font-size: 20px;
}
/* 覆盖层 */
.overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}
</style>



