<template>
  <div>
    <mu-appbar style="width: 100%;" color="#4ed995" class="warningHeader">
      <div slot="left" color="#3051f1">
        <a @click="toBack()">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">预警管理</span>
      </div>
      <div slot="right" class="icon" size="20" @click="openBotttomSheet">
        <mu-button icon slot="left" class="icon" size="20">
          <img src="static/img/queryNews.png" alt srcset />
        </mu-button>
      </div>
    </mu-appbar>
    <div class="statusTitle">
      <!-- <div>
          处理状态  
      </div>-->
      <div class="flex">
        <div @click="handled()" :style="styleHandle">已处理</div>
        <div @click="nohandled()" :style="styleNoHandle">未处理</div>
      </div>
    </div>
    <div class="warningContent">
      <!-- <mu-expansion-panel> -->

      <div style="padding:10px; background:white;margin:5px;height:100%;margin">
        <mu-container>
          <mu-expansion-panel
            v-for="(item,index) in listWarningContent"
            :key="index"
            :expand="panel === 'panel1'+index"
            @change="toggle('panel1'+index,index)"
            class="boxShow"
            style="padding:0 10px;"
          >
            <div slot="header" style="color:rgb(129, 119, 119)" class="headerListAlert">
              <div>{{item.gmtModified}}</div>
              <div>
                <button
                  class="btnStyles"
                  v-if="item.warningStatus===0"
                  @click="backTonoHandleNo(item.warningId)"
                >未处理</button>
                <button
                  class="btnStylesHandled"
                  v-if="item.warningStatus===1"
                  @click="backTonoHandle(item.warningId)"
                >已处理</button>
              </div>
            </div>
            <div>
              <div class="bettweenFlexBack">
                <div class="bettweenFlex">
                  <span>编号</span>
                  <span>{{item.warningId}}</span>
                </div>
                <div class="bettweenFlex">
                  <span>预警时间</span>
                  <span>{{item.gmtModified}}</span>
                </div>
                <div class="bettweenFlex">
                  <span>预警站点</span>
                  <span>{{item.warningStation}}</span>
                </div>
                <div class="bettweenFlex">
                  <span>处理人</span>
                  <span>{{item.processingPerson}}</span>
                </div>
                <div class="bettweenFlex">
                  <span>预警类型</span>
                  <span>{{item.warningType}}</span>
                </div>
                <div class="bettweenFlex">
                  <div>处理状态</div>
                  <div>
                    <button
                      class="btnStylesCont"
                      v-if="item.warningStatus===0"
                      @click="backTonoHandleNo(item.warningId)"
                    >未处理</button>
                    <button
                      class="btnStylesHandledCont"
                      v-if="item.warningStatus===1"
                      @click="backTonoHandle(item.warningId)"
                    >已处理</button>
                  </div>
                </div>
                <div class="panelContent" v-for="(itemChild,index1) in content" :key="index1">
                  <div class="bettweenFlex">
                    <span>{{itemChild[0].name}}</span>
                    <span style="color:red">{{itemChild[0].value}}</span>
                  </div>
                </div>
              </div>
            </div>
          </mu-expansion-panel>
        </mu-container>
        <mu-container>
          <mu-bottom-sheet :open.sync="open">
            <mu-list @item-click="closeBottomSheet">
              <mu-sub-header>
                <div class="queryBtn">筛选</div>
              </mu-sub-header>

              <mu-list-item>
                <div class="titleselect">
                  <span>站点名称：</span>
                </div>
                <div class="select" @click="selectDataPicoptions" id="classPicoptions">
                  <input
                    type="text"
                    placeholder="请选择站点"
                    class="input"
                    disabled
                    v-model="normal.value1"
                  />
                </div>
              </mu-list-item>
              <mu-list-item>
                <div class="titleselect">
                  <span>开始时间：</span>
                </div>
                <div class="select" @click="selectStartTime('start')">
                  <input type="text" class="input" disabled v-model="value1" />
                </div>
              </mu-list-item>
              <mu-list-item>
                <div class="titleselect">
                  <span>结束时间：</span>
                </div>
                <div class="select" @click="selectLastTime('end')">
                  <input type="text" class="input" disabled v-model="value2" />
                </div>
              </mu-list-item>
              <mu-list-item>
                <div class="btnBottom">
                  <div class="left" @click="closeBottomSheet()">取消</div>
                  <div class="right" @click="initData()">确定</div>
                </div>
              </mu-list-item>
            </mu-list>
          </mu-bottom-sheet>
        </mu-container>
        <mu-container>
          <mu-bottom-sheet :open.sync="openTime">
            <mu-list @item-click="closeBottomSheetTime">
              <mu-sub-header>筛选</mu-sub-header>
              <van-datetime-picker
                v-model="currentDate"
                type="date"
                :min-date="minDate"
                @confirm="confirmTime"
                @cancel="close"
              />
            </mu-list>
          </mu-bottom-sheet>
        </mu-container>
      </div>
    </div>
  </div>
</template>
<script>
import request from "../../../static/js/request.js";
import Warning from "../../../static/js/warning.js";
import WarningHandled from "../../../static/warning/index.js";
import AppBace from "../../../static/ajax.js";
export default {
  name: "warningControl",
  data() {
    return {
      listWarningContent: [],
      token: "",
      panel: "",
      content: [],
      openSimple: false,
      open: false,
      value1: AppBace.DateTime(30),
      value2: AppBace.filter(new Date()),
      options: [],
      normal: { value1: this.$store.state.menu[0] },
      styleHandle: "",
      showHandled: false,
      showNoHandled: false,
      styleNoHandle: "",
      list: [],
      openTime: false,
      currentDate: new Date(),
      minDate: new Date("1970-07-01"),
      Time: ""
    };
  },
  methods: {
    selectDataPicoptions() {
      this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicoptions, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.menu, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.normal.value1 = selectArr[0];
          _this.open = true;
        }
      });
    },

    close() {
      this.openTime = false;
      this.value1 = null;
      this.value2 = null;
      this.normal.value1 = null;
      this.initData();
    },
    confirmTime(value) {
      this.openTime = false;
      switch (this.Time) {
        case "start":
          this.value1 = AppBace.filter(value);
          break;
        case "end":
          this.value2 = AppBace.filter(value);
          break;
      }
    },
    closeBottomSheetTime() {
      this.openTime = false;
    },
    selectStartTime(e) {
      this.openTime = true;
      this.Time = e;
    },
    selectLastTime(e) {
      this.Time = e;
      this.openTime = true;
    },
    handled() {
      // var arr = this.listWarningContent;
      this.showHandled = !this.showHandled;
      if (this.showHandled) {
        var arr = this.$store.state.handle;
        this.styleHandle = "color:#ccc;border:none";
        if (!this.showNoHandled) {
          this.listWarningContent = WarningHandled.Processed(arr, 0);
        } else {
          this.listWarningContent = [];
        }
      } else {
        var arr = this.$store.state.handle;
        this.styleHandle = "";
        if (!this.showNoHandled) {
          this.listWarningContent = arr;
        } else {
          this.listWarningContent = WarningHandled.Processed(arr, 1);
        }
      }
    },
    nohandled() {
      var arr = this.$store.state.handle;
      this.showNoHandled = !this.showNoHandled;
      if (this.showNoHandled) {
        this.styleNoHandle = "color:#ccc;border:none";
        if (!this.showHandled) {
          this.listWarningContent = WarningHandled.Processed(arr, 1);
        } else {
          this.listWarningContent = [];
        }
      } else {
        this.styleNoHandle = "";
        if (!this.showHandled) {
          this.listWarningContent = arr;
        } else {
          this.listWarningContent = WarningHandled.Processed(arr, 0);
        }
      }
    },
    closeBottomSheet() {
      this.open = false;
      this.value1 = null;
      this.value2 = null;
      this.normal.value1 = null;
      this.initData();
    },
    openBotttomSheet() {
      this.open = true;
    },
    backTonoHandleNo(e) {
      // console.log(1);
      if (this.user !== "manager") {
        this.$router.push({
          path: "/TwoHandle",
          query: {
            id: e
          }
        });
      } else {
        this.$toast.warning("此用户只可以查看");
      }
    },
    openSimpleDialog(e) {
      this.openSimple = true;
      this.content = Warning.warningContent(this.listWarningContent, e);
      console.log(e);
    },
    closeSimpleDialog() {
      this.openSimple = false;
    },
    toggle(panel, index) {
      this.panel = panel === this.panel ? "" : panel;
      this.content = Warning.warningContent(this.listWarningContent, index);
      // debugger
    },
    backTonoHandle(e) {
      if (this.user !== "manager") {
        this.$router.push({
          path: "/Handle",
          query: {
            id: e
          }
        });
      } else {
        this.$toast.warning("此用户只可以查看");
      }
    },
    toBack() {
      // this.$router.go(-1);
      if (this.user === "manager") {
        this.$router.push({
          path: "/Navigation",
          query: {
            id: 0
          }
        });
      } else {
        this.$router.push({
          path: "/Navigation",
          query: {
            id: 2
          }
        });
      }
    },
    async initData() {
      this.open = false;
      var url = "/warning/list";
      var waterStation = this.normal.value1;
      // var waterStation = "沙井二期入水口水质监测站";
      var account = 0;
      if (this.value1 === null) {
        var starttime = null;
        var lasttime = null;
      } else {
        var starttime = AppBace.filterTime(this.value1);
        var lasttime = AppBace.filterTime(this.value2);
      }
      var pageSize = 500000000;
      var pageNum = 1;
      var res = await request.request(
        url,
        "POST",
        {
          waterStation,
          starttime,
          lasttime,
          pageSize,
          pageNum
        },
        this.token
      );
      console.log("res", res);
      this.$store.commit("warningContent", res.data.data.data);

      this.listWarningContent = res.data.data.data;
      this.list = this.listWarningContent;
      console.log(this.list);
      //  this.content =  Warning.warningContent(res.data.data.data);
      //  debugger
    }
  },
  mounted() {
    this.user = sessionStorage.getItem("user");
    this.token = sessionStorage.getItem("token");
    this.options = this.$store.state.menu;

    this.initData();
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
.bettweenFlex {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  height: 30px;
}
.bettweenFlexBack {
  color: rgb(129, 119, 119);
  background: white;
  border-radius: 10px;
  /* margin: 10px;
  padding: 10px; */
}
.btnStyles {
  border-radius: 5px;
  border: rgb(247, 78, 78) 2px solid;
  background: rgb(236, 162, 162);
  color: rgb(248, 19, 19);
  outline: none;
}
.btnStylesHandled {
  border-radius: 5px;
  border: rgb(92, 212, 248) 2px solid;
  background: rgb(192, 231, 250);
  color: rgb(25, 191, 241);
  outline: none;
}
.boxShow {
  box-shadow: none;
  /* border-radius: 10px; */
  margin-bottom: 5px;
}
.container {
  padding: 0;
}
.warningContent {
  margin-top: 106px;
}
.warningHeader {
  position: fixed;
  top: 0;
}
.btnStyle {
  border-radius: 5px;
  background: white;
  color: blue;
  outline: none;
  /* border:none; */
}
.flexBettween {
  display: flex;
  justify-content: space-between;
}
.headerListAlert {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}
.headerBtnBottom {
  text-align: center;
  font-size: 14px;
  background: #4ed995;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.mu-list {
  padding: 0;
  list-style: none;
}
.flex-direction {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
}
.flex-direction mu-list-item {
  /* width: 100%; */
  display: flex;
  flex: 1;
  padding: 0;
}
.mu-item {
  padding: 0;
}
.btn {
  height: 40px;
  padding: 0 15px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 0 5px;
  margin-top: 5px;
}
.titleWidth {
  width: 60px;
}
.flex {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.flex div {
  margin-left: 10px;
  border-bottom: #4ed995 solid 2px;
  /* box-shadow: 2px 2px 2px #ccc; */
  color: #4ed995;
  /* border-radius: 5px; */
  padding: 2px;
  /* background: orange; */
  font-size: 16px;
}
.statusTitle {
  padding-top: 15px;
  background: white;
  /* margin: 0 10px; */
  padding: 5px;
  position: fixed;
  top: 60px;
  width: 100%;
  /* border-radius: 10px; */
}
.query {
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #4ed995;
  color: white;
  font-weight: 600;
}
.queryNo {
  color: rgb(68, 67, 67);
  background: #ccc;
}
.paddingNo {
  padding: 0;
}
.icon img {
  width: 28px;
  height: 28px;
}
.btnStylesCont {
  color: rgb(248, 19, 19);
  outline: none;
}
.btnStylesHandledCont {
  color: rgb(25, 191, 241);
  outline: none;
}
</style>



