<script>
export default {
  data() {
    return {
      listgo1Status: true,
      listgo1Status1: false,
      disabled: false,
      goNumber: 0,
      GoName: sessionStorage.getItem("lastname"),
      goStatus: true,
      news: "暂无消息...",
      goStatus1: false,
      listGo: [],
      listGo1: [],
      type: "",
      path: "",
      menu: "",
      deleteData: "",
      token: sessionStorage.getItem("token"),
      open: false,
      address:""
    };
  },
  mounted() {
    // this.path = this.$router.history.current.path;
    // setInterval(() => {
    //   this.GoEasyInit();
    // }, 1000);
    this.GoEasyInit();
    //  this.$store.commit('deleteData');
    console.log("listGo", this.listGo.length);
    this.getAddress();
  },
  watch: {
    "$route.path": function(newVal, oldVal) {
      if (newVal === "/Navigation") {
        // console.log("欢迎进入zhu面");
        this.path = "/Navigation";
        this.menu = "";
      } else if (newVal === "/Data") {
        this.path = "/Data";
        this.menu = "数据统计";
      } else if (newVal === "/Control") {
        this.path = "/Control";
        this.menu = "运维管理";
      } else if (newVal === "/Settings") {
        this.path = "/Settings";
        this.menu = "设置";
      } else if (newVal === "/RealData") {
        this.path = "/RealData";
        this.menu = "实时数据";
      } else if (newVal === "/Add") {
        this.path = "/Add";
        this.menu = "运维新增";
      } else if (newVal === "/realMap") {
        this.path = "/Map";
        this.menu = "实时地图";
      } else if (newVal === "/WarningControl") {
        this.path = "/WarningControl";
        this.menu = "预警管理";
      } else if (newVal === "/TwoHandle") {
        this.path = "/TwoHandle";
        this.menu = "预警处理";
      } else if (newVal === "/Details1") {
        this.path = "/Details1";
        this.menu = "数据详情";
      } else if (newVal === "/Handle") {
        this.path = "/Handle";
        this.menu = "预警处理";
      } else if (newVal === "/Event") {
        this.path = "/Event";
        this.menu = "事件管理";
      } else if (newVal === "/EventList") {
        this.path = "/EventList";
        this.menu = "事件列表";
      } else if (newVal === "/EventUpload") {
        this.path = "/EventUpload";
        this.menu = "事件上传";
      } else if (newVal === "/eventDetails") {
        this.path = "/eventDetails";
        this.menu = "列表详情";
      } else if (newVal === "/controlDetails") {
        this.path = "/controlDetails";
        this.menu = "运维详情";
      } else if (newVal === "/PointDetails") {
        this.path = "/PointDetails";
        this.menu = "监控点数据详情";
      } else if (newVal === "/Warning") {
        this.path = "/Warning";
        this.menu = "预警统计";
      } else if (newVal === "/Leavingmessage") {
        this.path = "/Leavingmessage";
        this.menu = "意见反馈";
      } else if (newVal === "/serveMessage") {
        this.path = "/serveMessage";
        this.menu = "留言列表";
      } else if (newVal === "/historyData") {
        this.path = "/historyData";
        this.menu = "历史数据";
      } else if (newVal === "/historyDataD") {
        this.path = "/historyData";
        this.menu = "历史数据";
      } else if (newVal === "/Map") {
        this.path = "/Map";
        this.menu = "实时地图";
      } else if (newVal === "/Assistant") {
        this.path = "/Assistant";
        this.menu = "辅助决策";
      } else {
        this.path = "";
      }
      // if (newVal === "/Login") {
      //   console.log("欢迎进入denglu页面");
      // }
      // historyDataD
    }
  },
  methods: {
    getAddress() {
      //高德地图API
      //AMap.service解决只使用高德Geolocation功能提示Geolocation不是构造函数问题
      AMap.service(["AMap.Geolocation"], function() {
        var geolocation = new AMap.Geolocation();
        geolocation.getCurrentPosition(function(status, result) {
          if (status == "complete") {
            // attendanceVue.currentAddress.ADDRESS = result.formattedAddress; //设置当前地址
            // attendanceVue.currentCityCode = result.addressComponent.citycode; //设置当前城市code
            // //设置当前经纬度
            // attendanceVue.currentLngLat.push(result.position.lng);
            // attendanceVue.currentLngLat.push(result.position.lat);
            console.log(result);
            console.log(result.formattedAddress);
            this.address = result.addressComponent.district;
            // formattedAddress
          } else {
            // attendanceVue.currentAddress.ADDRESS = result.message;
            console.log(result.message);
          }
        });
      });
    },
    goNews(index, e) {
      // console.log(index);
      // console.log(e);
      if (sessionStorage.getItem("lastname") == "企业人员") {
        this.$toast.warning("您没有权限！！！");
      } else if (sessionStorage.getItem("lastname") == "运维人员") {
        // this.$router.push({ path: "/TwoHandle", query: { warningid: e } });
        this.$router.push({ path: "/Add", query: { id: e } });
      } else {
        this.$router.push({ path: "/TwoHandle", query: { warningid: e } });
      }
      this.open = false;
      // if(sessionStorage.getItem("lastname") == "运维人员")
      // this.shift = 2;
      // sessionStorage.setItem("navItem", 2);
    },
    GoEasyInit() {
      var that = this;
      var myWebsocket = new WebSocket(
        "ws://47.112.16.168:8800/websocket/" + sessionStorage.getItem("goEsay")
        // "ws://192.168.50.151:8800/websocket/" + sessionStorage.getItem("goEsay")
      );
      myWebsocket.onopen = function(event) {
        console.log("链接成功 ");
        // console.log(event);
        that.GoHttp();
      };
      myWebsocket.onmessage = function(event) {
        console.log("接受到消息：", event.data);
        // that.listGo.push(event.data);
        // debugger
        let obj = JSON.parse(event.data);
        that.NewsGo(obj.warningid, obj.type, obj.conc);
      };

      // console.log(this.listGo.length==0)
      // debugger

      // sessionStorage.setItem("goStatus", this.goStatus);
      // sessionStorage.setItem("goStatus1", this.goStatus1);
      // debugger
    },
    NewsGo(e, f, g) {
      if (f == "operate_o") {
        this.GoName = "运维提醒";
        this.type = "运维";
        if (this.listGo1.indexOf(e) === -1) {
          this.listGo1.push(e);
          if (this.listGo1.length == 0) {
            this.listgo1Status = true;
            this.listgo1Status1 = false;
          } else {
            this.listgo1Status = false;
            this.listgo1Status1 = true;
            // this.goNumber = this.listGo.length;
          }
        } else {
        }
        // this.listGo.push(e);
      } else if (f == "warning") {
        this.GoName = "预警提醒";
        this.type = "预警";
        // sessionStorage.setItem("GoName", this.GoName);
        // sessionStorage.setItem("type", this.type);
        // console.log("1", this.listGo.indexOf(e));
        if (this.listGo.indexOf(e) === -1) {
          this.listGo.push(e);
          // debugger
          if (this.listGo.length == 0) {
            this.goStatus = true;
            this.goStatus1 = false;
          } else {
            this.goStatus = false;
            this.goStatus1 = true;
            this.goNumber = this.listGo.length;
          }
          //   sessionStorage.setItem("listGo", this.listGo);
        }

        // sessionStorage.setItem("goNumber", this.listGo.length);
      } else if (f == "warningFinished") {
        this.deleteDataDevice(e, f);
      } else if (f == "operateFinished") {
        this.deleteDataDevice(e, f);
      } else if (f == "operate_r") {
        this.GoName = "运维提醒";
        this.type = "运维";
        this.disabled = true;
        if (this.listGo1.indexOf(e) === -1) {
          this.listGo1.push(e);
          if (this.listGo1.length == 0) {
            this.listgo1Status = true;
            this.listgo1Status1 = false;
          } else {
            this.listgo1Status = false;
            this.listgo1Status1 = true;
            // this.goNumber = this.listGo.length;
          }
        } else {
        }
      }
      // this.deleteDataDevice(f);
    },
    updataListGo(e) {
      this.listGo.splice(e, 1);
      this.goNumber = this.listGo.length;
      if (this.listGo.length === 0) {
        this.goStatus = true;
        this.goStatus1 = false;
      } else {
        this.goStatus = false;
        this.goStatus1 = true;
      }
    },
    deleteDataDevice(e, f) {
      // debugger
      if (this.listGo.length !== 0) {
        console.log(this.listGo.indexOf(e));
        if (this.listGo.indexOf(e) !== -1) {
          var num = this.listGo.indexOf(e);
          this.updataListGo(num);
        } else {
        }
      } else {
      }
    },
    GoHttp() {
      var Url = "http://47.112.16.168:8800/publish";
      // var Url = "http://192.168.50.151:8800/publish";
      //  var url = "http://47.112.16.168:8800/publish";
      var that = this;
      this.$http({
        method: "get",
        url: Url,
        headers: {
          token: that.token
        },
        data: {}
      })
        .then(res => {
          console.log("go", res);
          this.loading1 = false;
        })
        .catch(function(error) {
          console.log("出错了", error);
        });
    }
  }
  //   props: ["path"]
};
</script>
<style scoped>
.icon {
  /* border-radius: 100%; */
  background: #4ed99f;
  padding-right: 10px;
  height: 50px;
  line-height: 80px;
  /* float:right; */
}
#mapHeader #homeIcon {
  position: relative;
  top: 3px;
}
#historyDataD {
  position: fixed;
  top: 0;
}
