/*
 * 点击事件
 */
Vue.directive("tap", {
  bind: function(el, binding) {
    const touch = new TouchCls(el, binding, "tap");
    touch.initialize();
  }
});
/*
 * 长按事件
 */
Vue.directive("swipe", {
  bind: function(el, binding) {
    const touch = new TouchCls(el, binding, "swipe");
    touch.initialize();
  }
});
/*
 * 左滑
 */
